.To_do_container {
  position: relative;
}

.To_do_container::before,
.To_do_container::after {
  content: '';
  background-color: black;
  width: 105%;
  height: 4px;
  display: block;
  position: relative;
  right: 2.5%;
}

.To_do_x {
  position: relative;
}

.To_do_x::before,
.To_do_x::after {
  content: "";
  position: absolute;
  display: block;
  top: -7.5%;
  bottom: 0; 
  width: 4px; 
  background-color: black; 
  height: 115%;
}

.To_do_x::before {
  left: 0; /* Position to the left */
}

.To_do_x::after {
  right: 0; /* Position to the right */
}

.tasks,.num-tasks {
  font-family: "Patua One";
}

/* App.css */

/* styles.css */
.task-enter {
  opacity: 0;
  transform: translateY(-20px);
  max-height: 0;
  margin-bottom: 0;
  padding: 0;
}

.task-enter-active {
  opacity: 1;
  transform: translateY(0);
  max-height: 80px; /* Adjust based on the height of your list items */
  margin-bottom: 1rem; /* Adjust to match your layout */
  padding: 1rem; /* Adjust to match your layout */
  transition: opacity 500ms, transform 500ms, max-height 500ms, margin 500ms, padding 500ms;
}

/* Define exiting animation */
.task-exit {
  opacity: 1;
  transform: translateY(0);
  max-height: 80px;
  margin-bottom: 1rem;
  padding: 1rem;
}

.task-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  max-height: 0;
  margin-bottom: 0;
  padding: 0;
  transition: opacity 500ms, transform 500ms, max-height 500ms, margin 500ms, padding 500ms;
}

.To_do_title {
  font-family: "Pacifico", cursive;
  font-weight: 400;
  font-style: normal;
}

@media screen and (max-width:768px) {

  .To_do {
    background: white;
  }

  .To_do_container::before,
  .To_do_container::after {
    width: 0;
  }

  
.To_do_x::before,
.To_do_x::after {
  height: 0;
}

